
import { defineComponent, reactive, toRefs, watch, defineAsyncComponent, onBeforeMount, onMounted, computed , ComputedRef} from 'vue'
import { componentsConfig } from '@/config/report.config'
import { Modules } from '@/model/moduleObject'
import virtualList from '@/plugins/vue-virtual-scroll-list'
// import { elDragDialog } from '@/directives/dragDialog/index'
import Loading from '@/utils/loading'
import { formatModule } from '@/utils/module'
import { isArray } from '@/utils/validate'
import api from '@/http/api'
import { useRoute, useRouter } from 'vue-router'
import { useStore } from '@/store'

import Middle from './Middle.vue'
import { asyncFun } from '@/utils/async'
export default defineComponent({
	components: {
		virtualList
	},
	setup() {
		const itemComponent = Middle
		const { loading } = Loading()
		const store = useStore()
		const { params, query } = useRoute()
		let complete: ComputedRef<Boolean> = computed(() => {
			return store.state.app.complete
		})
		const dataMap = reactive({
			items: [{uid: 'unique_1', text: 'abc'}, {uid: 'unique_2', text: 'xyz'}, {uid: 'unique_3', text: 'ccd'}, {uid: 'unique_4', text: 'cdv'}],
			dialogTableVisible: false,
			components: Array<Modules>(),
			pageModeFullPage: true,
			pageMode: true,
			moudleList: computed(() => {
				return store.state.app.data
			})
				
		})
		watch(store.state.app.complete, (value: Boolean) => {
			console.log("complete", value )
			// done = value
		})
		watch(store.state.app.data, (value: any) => {
			store.state.app.data = value
		})
		// const loadingInstance = loading('请等待...')
		// setTimeout( () => {
		// 	loadingInstance.close()
		// }, 1000)
		const getModule = async() => {
			const params = {
				OrderId: query.orderId
			}
      		const res: any = await api.module.getModule(params)
			if (res && res.data) {
				const modules = formatModule(res.data)
				const items: Array<any> = []
				let index: number = 0
				modules.forEach((item: any) => {
					const mod = 'mod' + item.moduleId
					const file = componentsConfig[mod]
					if (file) {
						const componentItem: Modules = {
							id: ++index,
							name: mod,
							component: defineAsyncComponent(() => import(`@/${file}`)),
							columns: item.Columns
						}
						const params = {
							'enterpriseId': query.enterpriseId
						}
						items.push({name: mod.toUpperCase(), params, componentItem})
					}
				});
				const async = new asyncFun(items)
				store.commit('funcList', async)
			}
    	}
		onBeforeMount(() => {
			getModule()
		})
		onMounted(() => {
		})
		return {
			complete,
			...toRefs(dataMap),
			getModule,
			itemComponent
		}
	}
})
